body {
  background-color: #ddd !important;
}

body.install {
  margin-top: 30px;
}

.hidden {
  display: none !important;
}

template {
  display: block !important;
}

.full.width {
  width: 100%;
}

#code-editor {
  width: 650px;
  height: 250px;
}

.code-editor-container {
  height: 300px;
}

.font-size-normal {
  font-size: 1rem;
}

.text-align-center {
  text-align: center;
}

.clear {
  clear: both;
}

.install .ui.red.segment {
  padding-bottom: 70px;
}

.install .ui.form {
  padding-bottom: 25px;
}

html.ios {
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

html.ios,
html.ios body {
  height: initial !important;
}

.collapsible.item :hover {
  cursor: pointer;
}

.ui.pointing.menu .item:after {
  content: none !important;
}

.left.pointing.menu .toc.item {
  display: none;
}

.main-menu {
  margin-bottom: 15px !important;
}

body.pushable {
  background: #fff !important;
}

.ui.buttons.center.aligned {
  margin: 0 auto !important;
}

.ui.buttons .ui.button {
  margin-left: 5px !important;
}

.footer {
  margin-top: 15px !important;
}

.ui.buttons .ui.button:not(:first-child) {
  margin-left: 5px;
}

.center.aligned.item {
  text-align: center !important;
}

@media only screen and (max-width: 700px) {
  .ui.main-menu.menu {
    display: none !important;
  }
  .left.pointing.menu .item,
  .left.pointing.menu .menu {
    display: none;
  }
  .left.pointing.menu .toc.item {
    display: block;
  }
  .left-container {
    padding-top: 0 !important;
  }
  .left-container .menu {
    width: 100% !important;
    min-height: 3rem;
  }
  .sidebar.icon {
    float: left !important;
    display: block !important;
  }
  #main-container {
    width: 100% !important;
    margin-left: auto !important;
    margin-right: auto !important;
  }
  .tagsinput {
    width: 267px !important;
  }
  .index-nav {
    text-align: center !important;
  }
  .index-nav span {
    display: block;
    text-align: center;
  }
  .index-nav .ui.dropdown {
    width: 100% !important;
    padding: 5px;
    text-align: center;
  }
  .ace_editor {
    width: 300px !important;
  }
  .ui.message {
    font-size: 1.3rem;
  }
  .left.pointing.menu {
    font-size: 1.3rem !important;
  }
  .left.pointing.menu .toc.item {
    padding: 1rem !important;
  }
  .sidebar.menu.left {
    width: 100% !important;
    font-size: 1.3rem !important;
  }
  .pusher.dimmed {
    display: none !important;
  }
}

@media only screen and (min-width: 700px) and (max-width: 1100px) {
  #main-container {
    margin-left: 15px !important;
  }
  .left-container .menu {
    font-size: 0.85714rem !important;
    width: auto !important;
  }
  .main-menu {
    font-size: 1rem !important;
  }
  .main-menu span {
    display: none !important;
  }
  .left.pointing.menu {
    font-size: 1.3rem !important;
  }
  .left.pointing.menu .collapsible.item .menu .item {
    font-size: 0.9rem !important;
  }
  .index-nav {
    width: 100% !important;
  }
  .index-nav span {
    display: none !important;
  }
  .ace_editor {
    width: 518px !important;
  }
}

@media only screen and (min-width: 700px) {
  #main-container {
    min-width: 94% !important;
  }
}

@media only screen and (max-width: 700px) {
  .mobile.only:not(table) {
    display: block !important;
  }
  table.mobile.only {
    display: table !important;
  }
  .tablet.only {
    display: none !important;
  }
  .computer.only {
    display: none !important;
  }
  h4.mobile.clear-left {
    clear: left !important;
  }
}

@media only screen and (min-width: 700px) and (max-width: 1100px) {
  .tablet.only:not(table) {
    display: block !important;
  }
  table.tablet.only {
    display: table !important;
  }
  .mobile.only {
    display: none !important;
  }
  .computer.only {
    display: none !important;
  }
}

@media only screen and (min-width: 1100px) {
  .computer.only:not(table) {
    display: block !important;
  }
  table.computer.only {
    display: table !important;
  }
  .mobile.only {
    display: none !important;
  }
  .tablet.only {
    display: none !important;
  }
  .ui.two.cards .card {
    width: calc(33.33333333% - 2em);
  }
}

@media only screen and (min-width: 700px) {
  .computer.only.tablet.only:not(table) {
    display: block !important;
  }
  table.computer.only.tablet.only {
    display: table !important;
  }
}

.ui.sidebar.display-purposes {
  position: relative;
}

.architecture-block.active {
  border: 1px solid #56b4ef;
  -webkit-box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.05), 0 1px 0 rgba(255, 255, 255, 0.075);
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.05), 0 1px 0 rgba(255, 255, 255, 0.075);
}

.architecture-block:hover {
  border: 1px solid #56b4ef;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.05) inset, 0px 0px 8px rgba(82, 168, 236, 0.6);
}

